
.navbar {
  height: 50px;
  border-radius: 6px;
  background: #252935;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin: 0 auto;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  z-index: 1000;
  background: -webkit-gradient(linear, left top, left bottom, color-stop(10%, #252935), color-stop(90%, #282c39));
  background: linear-gradient(#252935 10%, #282c39 90%);
  border: 1px solid #20232e;
}
.navbar .nav-left {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
}
.navbar .nav-left .logo {
      width: 200px;
      height: 50px;
      background: url(../assets/logo.png) no-repeat 28px center;
      background-size: 84%;
      cursor: pointer;
}
.navbar .nav-left .lang {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      background: #1f232e;
      font-size: 12px;
      color: #525658;
      margin-left: 20px;
      border-radius: 3px;
      overflow: hidden;
      cursor: pointer;
}
.navbar .nav-left .lang li {
        padding: 2px 10px;
        text-indent: 10px;
        background-repeat: no-repeat;
        background-position: 6px center;
        background-size: 10px;
}
.navbar .nav-left .lang li:first-child {
          background-image: url("../assets/country/60.png");
}
.navbar .nav-left .lang li:last-child {
          background-image: url("../assets/country/60-1.png");
}
.navbar .nav-left .lang .active {
        background-color: #535bfb;
        color: #fff;
}
.navbar .nav-right {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    font-size: 12px;
}
.navbar .nav-right .active {
      background: #20232e;
}
.navbar .nav-right li {
      position: relative;
}
.navbar .nav-right li a {
        display: -webkit-inline-box;
        display: -ms-inline-flexbox;
        display: inline-flex;
        height: 50px;
        -webkit-box-align: center;
            -ms-flex-align: center;
                align-items: center;
        -webkit-box-pack: center;
            -ms-flex-pack: center;
                justify-content: center;
        color: #fff;
        padding: 0 20px;
        border-left: 1px solid #1e232e;
        font-weight: bold;
        font-size: 14px;
}
.navbar .nav-right li a:hover {
          background: #1e232e;
}
.navbar .nav-right li a:active {
          background: #4b2fe9;
}
.navbar .nav-right li .navBarCode {
        width: 124px;
        height: 160px;
        background: #735DEE;
        position: absolute;
        left: 1px;
        padding: 2px;
        font-size: 12px;
        text-align: center;
        color: #3f3190;
}
.navbar .nav-right li .navBarCode img {
          width: 100%;
}



