
.panel {
  background: #1f232e;
  margin: 0 auto;
  min-height: 600px;
  padding: 16px;
  margin-top: 10px;
  border-radius: 6px;
  position: relative;
}
.panel .panel-title-bar {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
    padding-bottom: 20px;
}
.panel .panel-title-bar .title {
      margin: 0;
      font-size: 32px;
      color: #fff;
}
.panel .panel-title-bar .close {
      font-size: 32px;
      display: -webkit-inline-box;
      display: -ms-inline-flexbox;
      display: inline-flex;
      -webkit-box-align: center;
          -ms-flex-align: center;
              align-items: center;
      color: #252935;
}
.panel .panel-child-tags {
    margin-top: 10px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    font-size: 14px;
    -ms-flex-wrap: wrap-reverse;
        flex-wrap: wrap-reverse;
    border-bottom: 1px solid #252935;
}
.panel .panel-child-tags .active {
      color: #1f232e;
      background: #fff;
      font-weight: bold;
}
.panel .panel-child-tags li {
      margin: 1px 1px 0 0;
      width: 96px;
      text-align: center;
      border-bottom: none;
      background: #252935;
      color: #515a6d;
      border-top-left-radius: 6px;
      border-top-right-radius: 6px;
      padding: 6px 12px;
      cursor: pointer;
      font-weight: 500;
}
.panel .panel-child-tags li:nth-child(8n) {
        margin-right: 0;
}
.panel .panel-items {
    margin-top: 10px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
}
.panel .panel-items li {
      width: 188px;
      height: 200px;
      background: #252935;
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-orient: vertical;
      -webkit-box-direction: normal;
          -ms-flex-direction: column;
              flex-direction: column;
      margin-right: 7px;
      margin-top: 7px;
      border-radius: 6px;
      overflow: hidden;
      position: relative;
}
.panel .panel-items li .isTao {
        font-size: 20px;
        position: absolute;
        right: 10px;
        top: 10px;
        background: #e06133;
        color: #fff;
        font-size: 16px;
        border-radius: 6px;
        padding: 2px;
        margin: 0;
}
.panel .panel-items li:nth-child(5n + 0) {
        margin-right: 0;
}
.panel .panel-items li a {
        display: block;
}
.panel .panel-items li a img {
          display: block;
          width: 100%;
          height: 130px;
}
.panel .panel-items li a .titleLink {
          font-size: 16px;
          margin: 10px;
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
          color: #fff;
          font-weight: bold;
}
.panel .panel-items li span {
        font-size: 12px;
        color: #515a6d;
        display: block;
        margin: 0 10px;
        text-overflow: ellipsis;
        white-space: nowrap;
        overflow: hidden;
}
.empty {
  width: 100%;
  height: 40px;
  text-align: center;
  line-height: 40px;
  font-size: 20px;
  border-radius: 4px;
  color: #fff;
  background: #c5b880;
  border: 1px solid #b5aa7a;
}
img[lazy=loading] {
  /*your style here*/
}
img[lazy=error], img[lazy=loaded] {
  /*your style here*/
  -webkit-animation-duration: 1s;
  animation-duration: 1s;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
  -webkit-animation-name: fadeIn;
  animation-name: fadeIn;
}
.fade-leave-active {
  -webkit-transition: opacity .3s;
  transition: opacity .3s;
}
.fade-leave-to {
  opacity: 0;
}
.fadeIn-enter-active {
  -webkit-animation-name: fadeInUp;
          animation-name: fadeInUp;
  -webkit-animation-duration: .3s;
          animation-duration: .3s;
  -webkit-animation-fill-mode: both;
          animation-fill-mode: both;
}
.fadeIn-leave-to {
  -webkit-animation-name: fadeOutDown;
          animation-name: fadeOutDown;
  -webkit-animation-duration: .3s;
          animation-duration: .3s;
  -webkit-animation-fill-mode: both;
          animation-fill-mode: both;
}
@-webkit-keyframes fadeIn {
from {
    opacity: 0;
}
to {
    opacity: 1;
}
}
@keyframes fadeIn {
from {
    opacity: 0;
}
to {
    opacity: 1;
}
}
@-webkit-keyframes fadeInUp {
from {
    opacity: 0;
    -webkit-transform: translateY(20px);
            transform: translateY(20px);
}
to {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
}
}
@keyframes fadeInUp {
from {
    opacity: 0;
    -webkit-transform: translateY(20px);
            transform: translateY(20px);
}
to {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
}
}
@-webkit-keyframes fadeOutDown {
from {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
}
to {
    opacity: 0;
    -webkit-transform: translateY(20px);
            transform: translateY(20px);
}
}
@keyframes fadeOutDown {
from {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
}
to {
    opacity: 0;
    -webkit-transform: translateY(20px);
            transform: translateY(20px);
}
}
