
.article {
  width: 920px;
  margin: 80px auto 40px auto;
  position: relative;
}
.article .title {
    color: #959EA8;
    position: absolute;
    font-size: 24px;
    top: -40px;
    font-weight: bold;
}
.article .article-cate {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
}
.article .article-cate li {
      width: 174px;
      height: 78px;
      border-radius: 4px;
      overflow: hidden;
      position: relative;
      cursor: pointer;
      border: 1px solid #261e53;
      -webkit-transition: all .3s;
      transition: all .3s;
}
.article .article-cate li:active {
        -webkit-transform: scale(0.96);
                transform: scale(0.96);
}
.article .article-cate li span {
        position: absolute;
        font-size: 12px;
        color: #fff;
        left: 6px;
        bottom: 6px;
        text-shadow: 1px 1px 0 #735DEE;
}



