
.search {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
}
.search select {
    border: none;
    -webkit-appearance: none;
       -moz-appearance: none;
            appearance: none;
    margin: 0;
    border-radius: 0;
    outline: none;
    padding: 0 30px 0 20px;
    background: #20232e url("data:image/png;base64,") 90% center no-repeat;
    background-size: 16%;
    color: #fff;
    font-size: 14px;
    position: relative;
}
.search .slogen {
    font-size: 38px;
    font-weight: bold;
    text-align: center;
    color: #20232e;
    margin-top: 20px;
}
.search .video {
    font-size: 50px;
    color: #735dee;
    -webkit-transition: all 0.3s;
    transition: all 0.3s;
}
.search .video:active {
      -webkit-transform: scale(0.96);
              transform: scale(0.96);
}
.search .search-bar {
    width: 992px;
    background: #20232e;
    height: 50px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    margin-top: 10px;
    border-radius: 6px;
    padding: 4px;
    overflow: hidden;
}
.search .search-bar input,
    .search .search-bar button {
      outline: none;
}
.search .search-bar input {
      background: #464c5c;
      width: 820px;
      font-size: 24px;
      color: #20232e;
      font-weight: bold;
      text-align: center;
      border-radius: 6px;
      border: 1px solid #252935;
      /* -webkit-box-shadow: 0 4px 10px rgba(0, 0, 0, 0.4) inset; */
      /* box-shadow: 0 4px 10px rgba(0, 0, 0, 0.4) inset; */
      -webkit-appearance: none;
}
.search .search-bar button {
      width: 60px;
      height: 50px;
      border: none;
      background: #20232e;
      color: #fff;
      font-size: 32px;
}
.search .search-bar button:active {
        background: #0b0c10;
}
.search .searchInfo {
    width: 920px;
    border-radius: 4px;
    height: 50px;
    background: #20232e;
    margin-top: 2px;
    display: -webkit-inline-box;
    display: -ms-inline-flexbox;
    display: inline-flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    color: #fff;
    font-size: 18px;
}



