
.tagWapper {
  margin: 20px auto 0 auto;
  position: relative;
}
.tagWapper .title {
    color: #959EA8;
    font-size: 24px;
    margin-bottom: 10px;
    font-weight: bold;
}
.tags {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}
.tags .active {
    background: #fff;
    color: #1f232e;
    font-weight: bold;
}
.tags li {
    width: 135px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    background: #363D4C;
    color: #959EA8;
    border-radius: 6px;
    font-size: 14px;
    height: 30px;
    padding: 4px 14px;
    margin: 2px 2px 0 0;
    background: -webkit-gradient(linear, left top, left bottom, color-stop(10%, #252935), color-stop(90%, #282c39));
    background: linear-gradient(#252935 10%, #282c39 90%);
    border: 1px solid #1f232e;
    cursor: pointer;
}
.tags li:nth-child(6n) {
      margin-right: 0;
}
.tags li:hover {
      background: #fff;
      color: #1f232e;
}
.tags li span {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-pack: center;
          -ms-flex-pack: center;
              justify-content: center;
      -webkit-box-align: center;
          -ms-flex-align: center;
              align-items: center;
      height: 38px;
      font-weight: bold;
      color: #535bfb;
      font-family: 'Roboto';
}



